.investor-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.investor-modal-box {
  background: white;
  padding: 24px;
  width: 90%;
  max-width: 420px;
  border-radius: 12px;
  position: relative;
  overflow-y: auto;
  max-height: 90vh;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.15);
}

.investor-modal-title {
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 24px;
}

.investor-form-input-with-icon,
.investor-form-select-with-icon {
  display: flex;
  align-items: center;
  border: 1px solid #e0e0e0;
  margin-bottom: 16px;
  border-radius: 8px;
  background: #fdfdfd;
  height: 44px;
}

.investor-form-input-icon {
  margin: 0 10px;
  font-size: 16px;
  color: #7f8c8d;
  min-width: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.investor-form-input-with-icon input {
  flex: 1;
  height: 100%;
  border: none;
  background: transparent;
  outline: none;
  font-size: 15px;
  padding: 0 5px;
}

.investor-form-select-with-icon .react-select__control {
  flex: 1;
  height: 42px;
  border: none;
  background: transparent;
  box-shadow: none;
  font-size: 15px;
  cursor: pointer;
  padding-left: 0;
}

.investor-form-select-with-icon .react-select__indicators {
  height: 42px;
}

.investor-form-select-with-icon .react-select__value-container {
  padding: 0 0 0 5px;
}

.investor-form-error-text {
  font-size: 12px;
  color: #e74c3c;
  margin-top: -12px;
  margin-bottom: 12px;
  display: inline-block;
}

.investor-form-terms-check {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  color: #444;
  margin: 16px 0;
}

.investor-form-terms-check input[type="checkbox"] {
  width: 16px;
  height: 16px;
  accent-color: #060644;
}

.investor-form-btn-group {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  margin-top: 16px;
}

.investor-form-back-btn,
.investor-form-submit-btn {
  flex: 1;
  padding: 10px;
  font-size: 15px;
  border: none;
  border-radius: 8px;
  color: white;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.investor-form-back-btn {
  background: #7f8c8d;
}
.investor-form-back-btn:hover {
  background: #6c757d;
}

.investor-form-submit-btn {
  background: #060644;
}
.investor-form-submit-btn:hover {
  background: #1f1d48;
}

.investor-modal-close-btn {
  position: absolute;
  top: 12px;
  right: 16px;
  background: none;
  border: none;
  font-size: 22px;
  color: #7f8c8d;
  cursor: pointer;
  transition: color 0.2s ease;
}
.investor-modal-close-btn:hover {
  color: #e74c3c;
}

.investor-form-password-field {
  position: relative;
}

.investor-form-toggle-eye {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  color: #666;
  font-size: 16px;
  cursor: pointer;
  z-index: 10;
}